import AddMaterialButton from '../../Materials/components/AddMaterialButton';
import React from 'react';
import {makeStyles} from '@material-ui/core/styles';

const MakeOrderMakeOrder = () => {
  const classes = useStyles();
  return (
        <div className={classes.makeClientCreatedBlock}>

        </div>
  )
}

const useStyles = makeStyles((theme) => ({
  makeClientCreatedBlock: {
    border: '1px solid #a7a0a0',
    boxShadow: '0px 10px 15px -5px rgba(0,0,0,0.5)',
    padding: '1%',
    paddingTop: '0',
    flexWrap: 'wrap',
    height: 'fit-content',
    marginTop: '20px',
    ['@media (max-width:1300px)']: {
      flexBasis: '100%',
      maxWidth: 'initial',
      marginBottom: '20px',
    },
    ['@media (max-width:668px)']: {
      border: 'none',
      boxShadow: 'none',
      margin: '0 5px',
    },
  },
}))

export default MakeOrderMakeOrder;
